.content-header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: calc(#{$content-header-height} - 20px);
  margin-bottom: 20px;
  padding: 10px 0;
  box-sizing: border-box;
  font-size: 20px;
  color: white;

  &__delay {
    // margin-top: -20px 0; // ....
    // padding: 20;
    padding-right: 25px;
    padding-left: 10px;
    width: 140px;
    height: 100%;
  }

  &__slider {
    height: 100%;
    width: 140px;
    margin-right: 20px;
    border-radius: none;
    font-family: inherit;
    font-size: inherit;
    color: goldenrod;
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-appearance: none;
    appearance: none;
    margin-top: -40px; // ....
  }

  &__drop {
    height: 100%;
    width: 160px;
    margin-right: 20px;
    border-radius: none;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-appearance: none;
    background: $oc-indigo-5;
    // background-image: url("data:image/svg+xml;utf8,<svg fill='white' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
    background-repeat: no-repeat;
    background-position-x: 100%;
    background-position-y: 5px;


    &:disabled {
      @extend %disabled;
    }
  }

  &__select {
    height: 100%;
    width: 160px;
    margin-right: 20px;
    border-radius: none;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-appearance: none;
    appearance: none;
    background: $oc-indigo-5;
    background-image: url("data:image/svg+xml;utf8,<svg fill='white' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
    background-repeat: no-repeat;
    background-position-x: 100%;
    background-position-y: 5px;
    padding-left: 10px;
    // padding: 10px 0;

    // &:disabled {
    //   @extend %disabled;
    // }
  }

  &__button {
    $button: &;
    display: flex;
    font-family: inherit;
    font-weight: bold;
    font-size: inherit;
    cursor: pointer;
    background: $oc-blue-5;
    appearance: none;
    -webkit-appearance: none;
    border: none;
    padding: 0 10px;
    margin-right: 20px;
    box-shadow: 0 3px 3px 0 $oc-blue-3;
    color: inherit;
    transition: transform 0.2s linear;
    outline: none;

    &--pause {
      @extend #{$button};
      background-color: $oc-teal-5;
    }

    &--usage {
      @extend #{$button};
      background-color: $oc-green-6;
      box-shadow: 0 3px 3px 0 $oc-green-3;
    }
    &--drop {
      @extend #{$button};
      height: 16px;
      width: 10px;
      font-size: 12px;
      // vertical-align: super;
    }

    &:last-child {
      margin-right: 0;
    }

    &:hover:enabled {
      transform: translateY(-10%);
    }

    &:disabled {
      @extend %disabled;
    }
  }
}